/** universal------------------------------------------------------------------------- */
* {
  font-family: "Bai Jamjuree", sans-serif;
}

/** elements ------------------------------------------------------------------------- */
a {
  color: inherit;
  text-decoration: none;
}

/** classes -------------------------------------------------------------------------- */
.temporary {
  font-size: 20px;
}

/** id's ----------------------------------------------------------------------------- */

#nav-bar {
  /* background-color: aqua; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 250px;
  margin-right: 250px;
  margin-top: 80px;
}

#name {
  font-size: 27px;
}

#title {
  font-size: 22px;
  color: rgb(170, 170, 170);
  position: relative;
  margin-left: 50px;
}

/* #page-link {
  font-size: 22px;
  color: rgb(170, 170, 170);
} */

.effect {
  color: rgb(170, 170, 170);
  position: relative;
  text-decoration: none;
  transition: color 0.4s ease-out;
}

.effect::after {
  border-radius: 50%;
  border-top: 3px solid black;
  content: "";
  position: absolute;
  right: 100%;
  bottom: 0.14em;
  left: 0;
  transition: right 0.4s cubic-bezier(0, 0.5, 0, 1), border-color 0.4s ease-out;
}

.effect:hover {
  text-decoration: none;
  color: black;
}

.effect:hover::after {
  right: 0;
  color: black;
}

#right {
  width: 600px;
  display: flex;
  justify-content: space-between;
}

/* profile content ------------------- */
#profile-cnt {
  display: flex;
  margin-top: 100px;
  display: flex;
}

#proPic-cnt {
  margin-left: 114px;
  width: 400px;
  text-align: center;
}

#profile-pic {
  width: 400px;
  margin-top: 130px;
}

#profile-pic:hover {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

#circle {
  border-radius: 50%;
  height: 400px;
  width: 400px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  animation-name: pic;
  animation-duration: 2s;
}

@keyframes pic {
  from {
    /* border-radius: 0%; */
    height: 10px;
    width: 10px;
  }
  to {
    /* border-radius: 50%; */
    height: 400px;
    width: 400px;
  }
}

#about-cnt {
  margin-top: 200px;
  width: 600px;
  display: flex;
  flex-direction: column;
  margin-left: 500px;
  border-left: 2px solid black;
  padding-left: 40px;
  animation-name: about;
  animation-duration: 2s;
}

@keyframes about {
  from {
    margin-left: 1000px;
  }
  to {
    margin-left: 500px;
  }
}

#about {
  border-bottom: 1px solid black;
  width: 140px;
}

